	public addJob(basic_info,serviceId): any {
		console.log(basic_info);
		let token = localStorage.getItem('login_token');
		const uri = environment.apiUrl+'user/createjobTemp';  //API
		let headers = new HttpHeaders();
		
		const obj = {
		  firstname: basic_info.firstname,
		  lastname: basic_info.lastname,
		  phone: basic_info.phone,
		  state :basic_info.state,
		  county :basic_info.county[0],
		  city :basic_info.city,
		  address1 :basic_info.address,
		  zipcode :basic_info.zipcode,
		  serviceId:serviceId,
		  jobStart:basic_info.job_start_date,
		  moving:[]
		};
		headers = headers.append('token', token); 
		 return this
		  .http
		  .post(uri, obj,{headers:headers}); 
	}
	
	
	public updateServicesInJobTemp(jobID,basic_info,type): any {
		
		let token = localStorage.getItem('login_token');
		const uri = environment.apiUrl+'user/updatejobTemp/';  //API
		let headers = new HttpHeaders();
		
		var obj = {};
		
		if(type == 'moving'){
			obj = {
			  step : 1,
			  moving:basic_info.moving,
			};
		}
		if(type == 'packing'){
			obj = {
			  step : 1,
			  packing:basic_info.packing,
			};
		}
		if(type == 'unpacking'){
			obj = {
			  step : 1,
			  unpacking:basic_info.unpacking,
			};
		}
		if(type == 'loading'){
			obj = {
			  step : 1,
			  loading:basic_info.loading,
			};
		}
		if(type == 'unloading'){
			obj = {
			  step : 1,
			  unloading:basic_info.unloading,
			};
		}
		
		headers = headers.append('token', token); 
		 return this
		  .http
		  .post(uri, obj,{headers:headers}); 
	}
	
