	public makePayment(stripetoken,planId,_user_id): any {
		let token = localStorage.getItem('login_token');
		const uri = environment.apiUrl+'partner/payment/'+_user_id;  //API partner/payment/5c90a8ac95339c2310392ac1
		let headers = new HttpHeaders();
		
		const obj = {
		  stripeToken: stripetoken,
		  planId: planId,
		};
		headers = headers.append('token', token); 
		 return this
		  .http
		  .post(uri, obj,{headers:headers}); 
	  }
	
	public updatePayment(stripetoken,planId,_user_id): any {
		let token = localStorage.getItem('login_token');
		const uri = environment.apiUrl+'partner/changeplan/'+_user_id;  //API partner/payment/5c90a8ac95339c2310392ac1
		let headers = new HttpHeaders();
		
		const obj = {
		  stripeToken: stripetoken,
		  planId: planId,
		};
		headers = headers.append('token', token); 
		 return this
		  .http
		  .post(uri, obj,{headers:headers}); 
	  }
	
	
	// Get state API   
	public getSavedJob(): any {
		let token = localStorage.getItem('login_token');
		let headers = new HttpHeaders();
		headers = headers.append('token', token); 
		
		const uri = environment.apiUrl+'user/getjobTemp'; //API 
		return this
			.http
            .get(uri,{headers:headers})
            .pipe(map(res => {
              return res;
            }));
	} 
