import { AdminService } from './../../../services/backend/admin.service';
import { Component, OnInit,OnDestroy, Inject } from '@angular/core';
import { HttpClient,HttpErrorResponse } from '@angular/common/http';
import {Router} from '@angular/router';
import { FormGroup,  FormBuilder,  Validators,FormArray,FormControl } from '@angular/forms';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { Subject } from 'rxjs';
export interface DialogData {
  animal: string;
  name: string;
  jobID:any;
}

@Component({
  selector: 'user-list',
  templateUrl: 'list.component.html'
})
export class ListComponent implements OnDestroy, OnInit {

 dtOptions: DataTables.Settings = {};
  users: any;
  dtTrigger: Subject<any> = new Subject();

  constructor(private http: HttpClient, private service: AdminService,private router: Router,public dialog: MatDialog) {}

  openDialog(jobId): void {
      const dialogRef = this.dialog.open(DialogOverviewExampleDialogPartnerAdmin, {
        width: '1000px',
        data: {jobID:jobId}
      });

      dialogRef.afterClosed().subscribe(result => {
        if(result == 'Success'){
          // this._flashMessagesService.show('job has been deleted successfully', { cssClass: 'alert-success',timeout:6000});
        }
        
      });
	  }

  ngOnInit(): void {
	  
	this.dtOptions = {
        pagingType: 'full_numbers',
		pageLength: 3,
        columnDefs: [ { orderable: false, targets: [3] } ]
    };  
	// Call gertuser service function 
	 var user='partner';
	 this.service.getUsers(user).subscribe(res => {
     console.log(res);
	   this.users = res;
	   this.dtTrigger.next();
	   // console.log(this.users);
	  
    },err=>{		
		console.log(err);
		     //   this.router.navigate(['login']);
		    
	});
	  
  }
  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  } 
}

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'dialog-overview-example-dialog.html',
  styleUrls : [ './dialog-overview-example-dialog.css' ]
})
export class DialogOverviewExampleDialogPartnerAdmin {
  jobID:any;jobData: any;button_text:any;
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialogPartnerAdmin>, 
     @Inject(MAT_DIALOG_DATA) public data: DialogData, private fb: FormBuilder,private service: AdminService) {	
      this.jobID = data.jobID;
      this.service.getinvoices(this.jobID).subscribe(res => {
        console.log(res);
          this.jobData = res.data.data;
          this.button_text = 'View More';
        }, err=> {		
          console.log(err); //   this.router.navigate(['login']);
      });
     }

	onNoClick(): void {
		this.dialogRef.close('ignore');
	}
}