import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient,HttpErrorResponse } from '@angular/common/http';
import { AdminService } from './../../../services/backend/admin.service';
import { Subject } from 'rxjs';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { FormGroup,  FormBuilder,  Validators, FormArray, FormControl } from '@angular/forms';
export interface DialogData {
  animal: string;
  name: string;
  jobID:any;
}

@Component({
  templateUrl: 'list.component.html'
})


export class ListComponent implements OnDestroy, OnInit {
  angForm: FormGroup;
  dtOptions: DataTables.Settings = {};
  services: any;
  show_dialog : boolean = false;
  result:any;
  startdate:any;
  result1:any;
  closeResult: string;
  dtTrigger: Subject<any> = new Subject();
	constructor(private http: HttpClient, private service: AdminService,private fb: FormBuilder,private router: Router,public dialog: MatDialog) {
    this.LoginForm();
  }
  
	LoginForm() {
		this.angForm = this.fb.group({
			selectedjobs: ['', Validators.required]
		});
  }

  openDialog(jobId): void {
		const dialogRef = this.dialog.open(DialogOverviewExampleDialogAdmin, {
		  width: '1000px',
		  data: {jobID:jobId}
		});

		dialogRef.afterClosed().subscribe(result => {
			if(result == 'Success'){
				// this._flashMessagesService.show('job has been deleted successfully', { cssClass: 'alert-success',timeout:6000});
			}
			
		});
	  }

   

  showjobs(event) {
    this.result = event.target.value;
    if(this.result != undefined && this.result != 'undefined') {
      this.service.getspecificjobs(this.result).subscribe(res => {
        console.log(res);
        if(res.jobs) {
          this.services = res.jobs;  
        } else {
          this.services = '';
        } 
      });
    } else {
      window.alert('Please select');
    }
    
  }

  ngOnInit(): void {
	  this.dtOptions = {
        pagingType: 'full_numbers',
	    	pageLength: 3,
        columnDefs: [ { orderable: false, targets: [3] } ]
    };  

    let array = [];

    // Call gertuser service function 
    this.service.getjobs().subscribe(res => {
      console.log(res);
      this.services = res.job;
		
      this.dtTrigger.next();
      }, err=> {		
        console.log(err); //   this.router.navigate(['login']);
    });
    
  }
  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
  } 
}

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'dialog-overview-example-dialog.html',
  styleUrls : [ './dialog-overview-example-dialog.css' ]
})

export class DialogOverviewExampleDialogAdmin {
  jobID:any;jobData: any;button_text:any;movingData:any;packingData:any;unpackingData:any;loadingData:any;unloadingData:any;
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialogAdmin>, 
     @Inject(MAT_DIALOG_DATA) public data: DialogData, private fb: FormBuilder,private service: AdminService) {	
      this.jobID = data.jobID;
      //http://18.223.97.46:3001/api/user/editjobs/5c9a0c509b7238289d9f8432
      this.service.getparticularjobs(this.jobID).subscribe(res => {
        console.log(res);
          this.jobData = res.job;
		  
		if(res.job.packing.length > 0){
			this.movingData = res.job.moving[0];
		}
		if(res.job.packing.length > 0){
			this.packingData = res.job.packing[0];
		}
		if(res.job.packing.length > 0){
			this.unpackingData = res.job.unpacking[0];
		}
		if(res.job.packing.length > 0){
			this.loadingData = res.job.loading[0];
		}
		if(res.job.packing.length > 0){
			this.unloadingData = res.job.unloading[0];
		}
          
          this.button_text = 'View More';
        }, err=> {		
          console.log(err); //   this.router.navigate(['login']);
      });
     }

	onNoClick(): void {
		this.dialogRef.close('ignore');
	}
}
