import { Component, OnInit } from '@angular/core';
import { Router,NavigationStart, NavigationEnd,ActivatedRoute,Event } from '@angular/router';
import { UserService } from './services/user.service';
import { Title } from '@angular/platform-browser';
import { SlimLoadingBarService } from "ng2-slim-loading-bar";

@Component({
  // tslint:disable-next-line
  selector: 'body',
  template: ' <router-outlet><ng2-slim-loading-bar color="#1695C2"></ng2-slim-loading-bar></router-outlet>'
})
export class AppComponent implements OnInit {
	localToken = '';notifications:any = ''; notificationCount:any = ''; 
  constructor(private lBar: SlimLoadingBarService, private router: Router,private userservice: UserService) { 
	
	this.router.events.subscribe((event: Event) => {  
      this.loadingBarInterceptor(event);  
    }); 
	
	
		/* this.router.events.subscribe((event) => {
			if (event instanceof NavigationEnd) {
				this.getNotificationCount();
			};
		}); */ 
		
  }
	private loadingBarInterceptor(event: Event) {  
		if (event instanceof NavigationStart) {  
		  this.lBar.start();  
		}  
		if (event instanceof NavigationEnd) {  
		  this.lBar.complete();  
		}      
	}
	/* getNotificationCount(){
		let token = localStorage.getItem('login_token');
		if(token){
			this.userservice.getNotificationCountForUser().subscribe(result => {
				this.notificationCount = result.count;
			});
		}
	} */
  ngOnInit() {
	  
    this.router.events.subscribe((evt) => {
		 if(localStorage.getItem('login_token')){
			return true;
		  }else{
			
		  }
      if (!(evt instanceof NavigationEnd)) {
        return;
      }

      window.scrollTo(0, 0);
    });
  }
 
}
