import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { navItems } from '../../frontend/_front_nav';
import { UserService } from '../../services/user.service';
import { JobService } from '../../services/job.service';
import { Router, NavigationEnd,Event } from '@angular/router';
import { ActivatedRoute,ActivationEnd } from '@angular/router';



@Component({
  selector: 'app-frontheader',
  templateUrl: './frontend-layout.component.html',
  styleUrls: [ './frontend-layout.component.css' ]
  
})
export class FrontendLayoutComponent implements OnInit {
  public navItems = navItems;
  //public sidebarMinimized = true; 
  private changes: MutationObserver;
  public element: HTMLElement;
  user_id = '';
  userdetail:any = ''; setFirstChar:any = '';jobservices:any = '';
  setusertype:any = '';  
  notificationCount:any=0;  notifications:any = '';
  logged_in = false;
  show_dialog : boolean = false;
  isLoggedIn:boolean=false;
	constructor(private router: Router,private activatedRoute : ActivatedRoute,private userservice: UserService,private jobservice: JobService,@Inject(DOCUMENT) _document?: any) {
		
			this.router.routeReuseStrategy.shouldReuseRoute = function(){
				return false;
			}	
		 this.changes = new MutationObserver((mutations) => {
			
		 // this.sidebarMinimized = _document.body.classList.contains('sidebar-minimized');
		});
		
		this.element = _document.body;
		this.changes.observe(<Element>this.element, {
		  attributes: true,
		  attributeFilter: ['class']
		}); 
		//this.getNotificationCount();
		/* this.router.events.subscribe((event) => {
			if (event instanceof NavigationEnd) {
				this.getLoggedInUser();
				this.getNotificationCount();
				
			};
		});  
 */
			this.getServices();
			
	}
	getServices(){
		this.jobservice.getServices().subscribe(result => {
			this.jobservices =  result.data;
		}); 
	}
	ngOnInit() {
		let token = localStorage.getItem('login_token');
		if(token){
			this.isLoggedIn =  true ;
			this.setusertype =  localStorage.getItem('user_role');
			this.userdetail = {firstname:localStorage.getItem('firstname'),lastname:localStorage.getItem('lastname'),customer_id:localStorage.getItem('customer_id'),totalbid:localStorage.getItem('total_bid')};
			this.setFirstChar = localStorage.getItem('firstname')[0];
		}else{
			this.router.navigate([this.router.url]);
			this.isLoggedIn =  false ;
		}
		
		//console.log('fist='+this.setFirstChar);
	}
	
	logout(){
		localStorage.clear();
		this.router.navigate(['/login']);
	} 
	ngOnDestroy(): void {
		this.changes.disconnect();
	
	}
	getLoggedInUser(){
		let token = localStorage.getItem('login_token');
		if(token){
			this.userservice.getUsers().subscribe(result => {
				this.userdetail = result[0];
			});
		}
	}
	getNotificationCount(){
		let token = localStorage.getItem('login_token');
		if(token){
			/* this.notificationCount = 0; */
			this.userservice.getNotificationCountForUser().subscribe(result1 => {
				this.notificationCount = result1.count;
			}); 
		}
	}
	getAllNotifications(){
		let token = localStorage.getItem('login_token');
		if(token){
			this.userservice.getAllNotifications(5).subscribe(result => {
				this.notifications = result.notfication;
			});
		}
	}
	readNotification(jobId){
		this.show_dialog = !this.show_dialog;
		
		let token = localStorage.getItem('login_token');
		if(token){
			this.userservice.readAllNotifications().subscribe(result => {
				
			});
		} 
	}
	toggle() {
		this.show_dialog = !this.show_dialog;
		
			if(this.show_dialog){
				this.userservice.getAllNotifications(5).subscribe(result => {
					console.log(result.notfication);
					this.notifications = result.notfication;
					this.notifications.forEach((x,key) => {
						var setApp = [];
						for(var i=0;i<this.jobservices.length;i++ ){
							var servic = x.jobId.serviceId;
							var _id = this.jobservices[i]._id;
							var _name = this.jobservices[i].name;
							$.each(servic,function(index,value){
								
								if(value != "" && value == _id){
									 var item = _name;
									 setApp[i] = item;
								 } 
							 });  		
						}
						var filtered = setApp.filter(function (el) {
						  return el != null;
						});
							
					
						this.notifications[key].service_name = filtered.join(',');
						
						
					});
				});
				
			}else{
				this.userservice.readAllNotifications().subscribe(result => {

				}); 
			}
	}
	/* isLoggedIn() {
		
		this.setusertype =  localStorage.getItem('user_role');
		let token = localStorage.getItem('login_token');
		if(token){
			return true ;
		}else{
			return false ;
		}
		
	} */
	setuserpartner(usertype){
		localStorage.setItem('usertype',usertype);
	}
	 
}
