import { Injectable } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { UserService } from './services/user.service';


@Injectable()
export class Globals {
	role: string = 'test';
	notifications:any = ''; notificationCount:any = ''; 
	constructor(private router: Router,private userservice: UserService) { 
		
		this.router.events.subscribe((event) => {
			if (event instanceof NavigationEnd) {
				this.getAllNotifications();
				this.getNotificationCount();
			};
		}); 
		
	}
	getAllNotifications(){
		let token = localStorage.getItem('login_token');
		if(token){
			this.userservice.getAllNotifications(5).subscribe(result => {
				this.notifications = result.notfication;
				
			});
		}
	}
	getNotificationCount(){
		let token = localStorage.getItem('login_token');
		if(token){
			this.userservice.getNotificationCountForUser().subscribe(result => {
				this.notificationCount = result.count;
			});
		}
	}
	ngOnInit() {
		
	}		
}